<template>
	<div :style="orderBlockStyle" class="order-block fx-shadow-block" @click="onClick">
		<div class="block-title">
			<i class="block-icon"></i>
			<span class="title-text">{{data.rdcDTO.name}}</span>
			<stateTag :billState="data.billState"></stateTag>
			<span v-show="data.difference" class="difference-span">有差异</span>
		</div>
		<div class="block-value">
			<div class="order-No">{{data.shopDTO.name}}</div>
			<div class="order-date">
				<span>业务日期：</span>
				<span>{{data.businessDate | filterDateYMD}}</span>
				<van-icon name="arrow" class="order-date-arrow"/>
			</div>
		</div>
		<div class="block-value">
			<div v-show="$fxAuth('distributionInBill.seewithtaxmoney')" class="order-No fx-price-font">
				<span>含税金额：</span>
				<span>￥{{toFixedNumberSize(data.taxMoney)}}</span>
			</div>
			<div class="order-date">
				<span>到货日期：</span>
				<span>{{data.arriveDate | filterDateYMD}}</span>
				<van-icon name="arrow" class="order-date-arrow"/>
			</div>
		</div>
		<div>
				<template v-if="data.houseName">
					<span>入库仓库:</span>
					<span class="order-warehouse">{{data.houseName}}</span>
				</template>
		</div>
		<checkBox :visibleFlag="showCheckBox" :checked="data.checked" class="order-checkbox"></checkBox>
	</div>
</template>

<script>
import checkBox from '@/components/common/CheckBox.vue'
import stateTag from '@/components/common/StateTag.vue'
import { mapState } from 'vuex'
export default {
	name: 'orderBlock',
	components: {
		checkBox,
		stateTag
	},
	props: {
		data: {
			type: Object,
			default: () => {
				return {}
			}
		},
		showCheckBox: {
			type: Boolean,
			default: false
		}
	},
	computed: {
		...mapState({
			sysPointSize: 'sysPointSize'
		}),
		orderBlockStyle () {
			let paddingLeft = this.showCheckBox ? '50px' : '15px'
			return { 'paddingLeft': paddingLeft }
		},
		toFixedNumberSize (data) {
			return (data) => {
				return parseFloat(this.$fxUtils.toFixedSHW(data, this.sysPointSize))
			}
		}
	},
	methods: {
		onClick () {
			if (this.showCheckBox) {
				this.$set(this.data, 'checked', !this.data.checked)
			} else {
				this.$emit('on-click', this.data)
			}
		}
	}
}
</script>
<style lang="stylus" scoped>
@import '~$assets/stylus/varsty.styl'
.order-block {
	width:320px
	margin:10px auto
	padding:15px
	text-align:left
	position:relative
	.block-title {
		height:50px
		line-height:50px
		font-size:14px
		font-weight:700
		.block-icon {
			width:20px
			height:20px
			background-image:url('~images/icon/icon-shop.png')
			background-repeat:no-repeat
			background-position: center
			background-size: 16px
			display:inline-block
			vertical-align: middle;
			margin-right:5px
		}
		.title-text {
			display:inline-block
			width:170px
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			vertical-align: middle;
			height: 50px;
			line-height: 50px;
			margin-right:5px
		}
		.difference-span {
			margin-left:10px
			color:$fxRed
		}
	}
	.block-value {
		display: flex;
		flex-direction: row;
		.order-No {
			flex:1
		}
		.order-date {
			flex:1
			text-align:right
			.order-date-arrow {
				position: relative;
				top: 1px;
			}
		}
	}
	.order-checkbox {
		position:absolute
		top: 50%;
		left: 14px;
		transform:translateY(-50%)
	}
}
</style>
